import { getLenis } from 'src/plugins/lenis';

import { refreshScroll } from '$utils/refresh-scroll';

export function initScroll() {
  const horizontalWrapper = document.querySelector('.scroll-wrap');
  const panels = gsap.utils.toArray('[data-panel]') as HTMLElement[];
  const totalScrollWidth = horizontalWrapper.scrollWidth;
  const viewportWidth = window.innerWidth;
  const scrollDistance = totalScrollWidth - viewportWidth;

  const mm = gsap.matchMedia();

  let scrollTriggerInstance;

  // Buat ScrollTrigger hanya di desktop
  mm.add(
    {
      isDesktop: '(min-width: 992px)',
      isMobile: '(max-width: 991px)',
    },
    (context) => {
      const { isDesktop } = context.conditions;
      let tween;

      // ==========================================================
      // 🖥️ DESKTOP MODE (Horizontal Scroll)
      // ==========================================================
      if (isDesktop) {
        const totalScrollWidth = horizontalWrapper.scrollWidth;
        const viewportWidth = window.innerWidth;
        const scrollDistance = totalScrollWidth - viewportWidth;

        const horizontalTween = gsap.to(horizontalWrapper, {
          x: -scrollDistance,
          ease: 'none',
          scrollTrigger: {
            id: 'horizontalScroll',
            trigger: '.page-main',
            start: 'top top',
            end: `+=${scrollDistance}`,
            scrub: 1,
            pin: true,
            anticipatePin: 1,
          },
        });

        tween = horizontalTween;
      }

      // ==========================================================
      // ✨ UNIVERSAL ANIMATION SETUP (shared logic)
      // ==========================================================
      const splitElements = document.querySelectorAll('[data-split-lines]');
      const aosItem = document.querySelectorAll('[data-aos]');
      const splitTargets = document.querySelectorAll('[data-heading]');

      splitElements.forEach((container) => {
        const splitRefs = [];
        const lines = [];
        const delay = parseFloat(container.getAttribute('data-delay')) || 0;
        const animateOnScroll = container.getAttribute('data-scroll') !== 'false';

        const elements = container.hasAttribute('data-copy-wrapper')
          ? Array.from(container.children)
          : [container];

        elements.forEach((element) => {
          const split = SplitText.create(element, {
            type: 'lines',
            mask: 'lines',
            linesClass: 'line++',
            lineThreshold: 0.1,
          });

          splitRefs.push(split);
          lines.push(...split.lines);
        });

        gsap.set(lines, { y: '110%' });

        const animationProps = {
          y: '0%',
          duration: 1,
          stagger: 0.1,
          ease: 'power4.out',
          delay,
        };

        if (animateOnScroll) {
          gsap.to(lines, {
            ...animationProps,
            scrollTrigger: {
              trigger: container,
              ...(isDesktop
                ? { containerAnimation: tween, start: 'left 75%' }
                : { start: 'top 75%' }),
              once: true,
            },
          });
        } else {
          gsap.to(lines, animationProps);
        }

        return () => splitRefs.forEach((s) => s?.revert());
      });

      aosItem.forEach((item) => {
        gsap.set(item, { autoAlpha: 0, y: '3em' });

        gsap.to(item, {
          autoAlpha: 1,
          y: '0em',
          ease: 'power2.out',
          duration: 1,
          scrollTrigger: {
            trigger: item,
            ...(isDesktop
              ? { containerAnimation: tween, start: 'left 75%' }
              : { start: 'top 95%' }),
            once: true,
          },
        });
      });

      splitTargets.forEach((el) => {
        const split = new SplitText(el, {
          type: 'chars, words, lines',
          wordsClass: 'word',
          charsClass: 'char',
          linesClass: 'line',
        });

        gsap.set(split.chars, { yPercent: 101, autoAlpha: 0 });

        ScrollTrigger.create({
          trigger: el,
          ...(isDesktop ? { containerAnimation: tween, start: 'left 75%' } : { start: 'top 75%' }),
          onEnter: () => {
            gsap.to(split.chars, {
              yPercent: 0,
              autoAlpha: 1,
              duration: 1.25,
              ease: 'primary-ease-out',
              stagger: { amount: 0.2, from: 'start' },
            });
          },
        });
      });

      // 🧹 Cleanup otomatis saat media query berubah
      return () => {
        ScrollTrigger.getAll().forEach((st) => st.kill());
        gsap.set(horizontalWrapper, { clearProps: 'all' });
      };
    }
  );

  // Hitung posisi X tiap panel
  const panelPositions = [];
  let cumulativeWidth = 0;
  panels.forEach((panel) => {
    const panelWidth = panel.offsetWidth;
    panelPositions.push(cumulativeWidth);
    cumulativeWidth += panelWidth;
  });

  // Event klik nav
  document.querySelectorAll('[data-scroll-to]').forEach((link) => {
    link.addEventListener('click', (e) => {
      e.preventDefault();
      const targetIndex = parseInt(link.getAttribute('data-scroll-to'), 10);
      const targetPanel = panels[targetIndex];

      if (window.innerWidth > 1024 && scrollTriggerInstance) {
        // Mode desktop → horizontal scroll via ScrollTrigger
        const targetX = panelPositions[targetIndex] || 0;
        const { start, end } = scrollTriggerInstance;
        const targetScrollY = start + (targetX / scrollDistance) * (end - start);

        gsap.to(window, {
          scrollTo: { y: targetScrollY, autoKill: false },
          duration: 1.2,
          ease: 'power2.out',
        });
      } else {
        // Mode mobile/tablet → scroll biasa
        targetPanel.scrollIntoView({ behavior: 'smooth', block: 'start' });
      }
    });
  });
}

export function initLoad() {
  //preload animation
  const preload = document.querySelector('.preload');
  const trainWrap = preload.querySelector('.train-wrap');
  const trainInner = preload.querySelector('.train-inner');
  const trainLeft = preload.querySelector('.train-bg.is-left');
  const doorLeft = preload.querySelector('.pintu.is-left');
  const doorRight = preload.querySelector('.pintu.is-right');
  const trainRight = preload.querySelector('.train-bg.is-right');
  const preloadInner = preload.querySelector('.preload-inner');

  // load animation
  const nav = document.querySelector('.navbar');
  const hero = document.querySelector('.hero-wrapper');
  const title = hero.querySelector('.title-wrap');
  const heading = hero.querySelector('.h1');
  const subtitle = hero.querySelector('.hero-subtitle');
  const btns = hero.querySelectorAll('.btn.is-swoosh');
  const cards = hero.querySelectorAll('.hero-card');

  const splitTittle = new SplitText(title, {
    type: 'lines',
    mask: true,
    lineThreshold: 0.1,
  });

  const splitHeading = new SplitText(heading, {
    type: 'lines, words, chars',
    lineThreshold: 0.1,
  });

  const splitSubtitle = new SplitText(subtitle, {
    type: 'lines',
    mask: true,
    lineThreshold: 0.1,
  });
  const loadTl = gsap.timeline({ paused: true });

  loadTl
    .set(nav, { autoAlpha: 0, y: '-2em' })
    .set(splitTittle.lines, { opacity: 0, y: '100%' })
    .set(splitHeading.chars, { opacity: 0, y: '100%' })
    .set(splitSubtitle.lines, { opacity: 0, y: '100%' })
    .set(btns, { autoAlpha: 0, y: '2em' })
    .set(cards, { autoAlpha: 0, scale: 0.75 });

  const preloadTl = gsap.timeline({
    onComplete: () => {
      gsap.to(preload, {
        autoAlpha: 0,
        scale: 0,
        duration: 0.8,
        ease: 'primary-ease',
        delay: 0.5,
      });
      loadTl.play();
    },
    paused: true,
  });

  gsap.set(preloadInner, { scale: 0.7 });

  preloadTl
    // .to(trainInner, { x: '-30vw', duration: 2.5, ease: 'power2.out' })
    .to(trainWrap, { scale: 1.8, duration: 0.8, ease: 'primary-ease' })
    .to(trainLeft, { clipPath: 'inset(0 15vw 0 0)', duration: 0.8, ease: 'power2.out' }, '>0.4')
    .to(doorLeft, { x: '-100%', duration: 0.8, ease: 'power2.out' }, '<')
    .to(doorRight, { x: '100%', duration: 0.8, ease: 'power2.out' }, '<')
    .to(trainRight, { clipPath: 'inset(0 0 0 15vw)', duration: 0.8, ease: 'power2.out' }, '<')
    .to(trainWrap, { scale: 8, duration: 0.8, ease: 'primary-ease' }, '>0.6')
    .to(preloadInner, { scale: 1, duration: 0.8, ease: 'primary-ease' }, '<');

  loadTl
    .to(nav, { autoAlpha: 1, y: '0em', duration: 1.2, delay: 1.2, ease: 'power2.out' })
    .to(
      splitTittle.lines,
      {
        opacity: 1,
        y: '0%',
        duration: 1,
        ease: 'power4.out',
        stagger: 0.1,
      },
      '<0.2'
    )
    .to(
      splitHeading.chars,
      {
        opacity: 1,
        y: '0%',
        duration: 1,
        ease: 'primary-ease-out',
        stagger: { amount: 0.4, from: 'start' },
      },
      '<0.2'
    )
    .to(
      splitSubtitle.lines,
      {
        opacity: 1,
        y: '0%',
        duration: 1,
        ease: 'power2.out',
        stagger: 0.1,
      },
      '<0.4'
    )
    .to(
      btns,
      {
        autoAlpha: 1,
        y: '0em',
        duration: 1,
        ease: 'power2.out',
      },
      '<0.2'
    )
    .to(
      cards,
      {
        autoAlpha: 1,
        scale: 1,
        duration: 1.2,
        ease: 'power2.out',
        stagger: {
          each: 0.05,
        },
      },
      '<0.2'
    );

  // Mulai animasi preload
  preloadTl.play();
}

export function initFaqAccordion(containerSelector = '.faq-content', allowMultipleOpen = false) {
  const container = document.querySelector(containerSelector);
  if (!container) return;

  const items = container.querySelectorAll('.faq-item');

  items.forEach((item, index) => {
    const text = item.querySelector('.faq-item-content');
    const icon = item.querySelector('.faq-icon');
    if (!text || !icon) return;

    // Set ikon awal (kalau pakai rotate)
    gsap.set(icon, { rotate: 0 });
    gsap.set(text, { height: 0, autoAlpha: 0 });

    const tl = gsap.timeline({ paused: true, reversed: true });

    tl.to(text, {
      height: 'auto',
      duration: 0.4,
      ease: 'none',
    })
      .to(
        text,
        {
          autoAlpha: 1,
          duration: 0.2,
          ease: 'none',
        },
        0
      )
      .to(
        icon,
        {
          rotate: 45,
          duration: 0.3,
          ease: 'none',
        },
        0
      ); // ikon animasi dimulai bersamaan

    item._accordionTimeline = tl;

    // Event klik
    item.addEventListener('click', (e) => {
      if (e.target.closest('.faq-item-content')) return;

      const isOpen = !tl.reversed();

      if (!allowMultipleOpen) {
        items.forEach((other) => {
          if (other !== item && !other._accordionTimeline.reversed()) {
            other.classList.remove('is-active');
            other._accordionTimeline.reverse();
          }
        });
      }

      if (isOpen) {
        item.classList.remove('is-active');
        tl.reverse();
      } else {
        item.classList.add('is-active');
        tl.play();
      }
      refreshScroll();
    });

    // === Auto open item pertama ===
    if (index === 0) {
      item.classList.add('is-active');
      tl.play(0);
    }
  });
}

export function initNav() {
  const links = document.querySelectorAll('.nav-link');

  links.forEach((link) => {
    const text = link.querySelectorAll('.nav-link-text');

    const tl = gsap.timeline({ paused: true, reversed: true });
    tl.to(text, {
      yPercent: -100,
      duration: 0.2,
      ease: 'primary-ease',
      stagger: 0.1,
    });

    link.addEventListener('mouseenter', () => {
      tl.play();
    });

    link.addEventListener('mouseleave', () => {
      tl.reverse();
    });
  });
}

export function initBtn() {
  const btn = document.querySelector('.faq-button-wrap');
  const icon = btn.querySelector('.faq-icon');
}
