export function initTrMarquee() {
  // Helper function untuk parsing atribut
  function parseAttr(defaultValue, attr) {
    const type = typeof defaultValue;
    if (typeof attr !== 'string' || attr.trim() === '') return defaultValue;

    if (attr === 'true' && type === 'boolean') return true;
    if (attr === 'false' && type === 'boolean') return false;
    if (!isNaN(attr) && type === 'number') return +attr;

    return attr; // jika bukan boolean/number, kembalikan string
  }

  // Ambil semua elemen marquee
  $("[tr-marquee-element='component']").each(function () {
    const $component = $(this);
    const $panels = $component.find("[tr-marquee-element='panel']");
    const $hoverTrigger = $component.find("[tr-marquee-element='triggerhover']");
    const $clickTrigger = $component.find("[tr-marquee-element='triggerclick']");

    // Baca atribut custom
    let speed = parseAttr(100, $component.attr('tr-marquee-speed'));
    const vertical = parseAttr(false, $component.attr('tr-marquee-vertical'));
    const reverse = parseAttr(false, $component.attr('tr-marquee-reverse'));
    const scrollDirectionControl = parseAttr(false, $component.attr('tr-marquee-scrolldirection'));
    const scrollScrub = parseAttr(false, $component.attr('tr-marquee-scrollscrub'));

    // Variabel internal
    let distance = -100;
    let timeScaleDefault = 1;
    let isPaused = false;

    if (reverse) distance = 100;

    // Function untuk pause/resume marquee
    function togglePause(paused) {
      isPaused = paused;

      const controlObj = { value: 1 };
      const tl = gsap.timeline({
        onUpdate: () => marqueeTimeline.timeScale(controlObj.value),
      });

      if (paused) {
        tl.fromTo(controlObj, { value: timeScaleDefault }, { value: 0, duration: 0.5 });
        $clickTrigger.addClass('is-paused');
      } else {
        tl.fromTo(controlObj, { value: 0 }, { value: timeScaleDefault, duration: 0.5 });
        $clickTrigger.removeClass('is-paused');
      }
    }

    // Buat timeline marquee
    const marqueeTimeline = gsap.timeline({
      repeat: -1,
      onReverseComplete: () => marqueeTimeline.progress(1), // agar looping lancar
    });

    // Animasi utama: horizontal atau vertical
    if (vertical) {
      speed = $panels.first().height() / speed;
      marqueeTimeline.fromTo(
        $panels,
        { yPercent: 0 },
        { yPercent: distance, ease: 'none', duration: speed }
      );
    } else {
      speed = $panels.first().width() / speed;
      marqueeTimeline.fromTo(
        $panels,
        { xPercent: 0 },
        { xPercent: distance, ease: 'none', duration: speed }
      );
    }

    // ScrollTrigger untuk kontrol kecepatan berdasarkan scroll
    const scrollObj = { value: 1 };
    ScrollTrigger.create({
      trigger: 'body',
      start: 'top top',
      end: 'bottom bottom',
      onUpdate: (st) => {
        if (!isPaused) {
          // Kontrol direction
          if (scrollDirectionControl && timeScaleDefault !== st.direction) {
            timeScaleDefault = st.direction;
            marqueeTimeline.timeScale(st.direction);
          }

          // Kontrol kecepatan berbasis velocity
          if (scrollScrub) {
            let t = 0.006 * st.getVelocity();
            t = gsap.utils.clamp(-20, 20, t);
            gsap
              .timeline({
                onUpdate: () => marqueeTimeline.timeScale(scrollObj.value),
              })
              .fromTo(scrollObj, { value: t }, { value: timeScaleDefault, duration: 0.5 });
          }
        }
      },
    });

    // Event hover untuk pause/resume (pointer fine = mouse)
    if (window.matchMedia('(pointer: fine)').matches) {
      $hoverTrigger.on('mouseenter', () => togglePause(true));
      $hoverTrigger.on('mouseleave', () => togglePause(false));
    }

    // Event klik untuk pause/resume
    $clickTrigger.on('click', function () {
      if ($(this).hasClass('is-paused')) {
        togglePause(false);
      } else {
        togglePause(true);
      }
    });
  });
}
